.scroller {
    position: relative;
    height: 100%; }

.container-main {
    padding-left: $navigationWidth;
    overflow-x: hidden;
    height: 100%;

    @media screen and (max-width: $thresholdCollapse) {
        padding-left: 0; } }

.container-navigation {
    width: $navigationWidth;
    background-color: $colorDarkBG1;
    @include position(fixed, 0 null null 0);
    height: 100%;
    z-index: 100;
    color: $colorLightText;

    @media screen and (max-width: $thresholdCollapse) {
        @include transition(all $durationMedium);
        left: -$navigationOffset;
        &--slide {
            @include transform(translateX($navigationOffset)); } }

    @media screen and (max-width: $thresholdMicro) {
        width: $navigationWidthNarrow; } }

.hamburger {
    display: inline-block;
    @include position(fixed, 0.5em null null 0.5em);
    font-size: 1.5em;
    @include size(2em);
    text-align: center;
    color: $colorText;
    line-height: 2em;
    z-index: -1;
    opacity: 0;

    @media screen and (max-width: $thresholdCollapse) {
        z-index: 130;
        opacity: 1;
        @include transition(color $durationShort, z-index 0ms ease $durationMedium, opacity $durationShort ease $durationMedium);

        &--open {
            @include transition(color $durationShort);
            z-index: -1;
            opacity: 0; } } }

.container-content {
    position: relative;
    z-index: 110;
    background-color: $colorLightBG3;
    min-height: 100vh;

    @media screen and (max-width: $thresholdCollapse) {
        @include transition(all $durationMedium);
        box-shadow: $shadowLeft;

        &--slide {
            @include transform(translateX($navigationWidth)); } }

    @media screen and (max-width: $thresholdMicro) {
        &--slide {
            @include transform(translateX($navigationWidthNarrow)); } } }

.content-whiteout {
    @include position(absolute, 0 null null 0);
    @include size(100%);
    opacity: 0;
    z-index: -1;
    background-color: $colorWhiteout;
    @include transition(opacity $durationMedium, z-index 0 ease $durationMediumUp);

    @media screen and (max-width: $thresholdCollapse) {
        &--active {
            z-index: 120;
            opacity: 1;
            @include transition(opacity $durationMedium);
            cursor: pointer; } } }

.content {
    padding-bottom: 3em; }

.no-touch {
    .hamburger:hover {
        color: $colorEmphasis; } }
