$durationModal: 300ms;
$durationModalUp: 350ms;

.modal {
    @include position(fixed, 0 null null 0);
    @include size(100%);
    z-index: -1;
    opacity: 0;
    @include transition(opacity $durationModal, z-index 0ms ease $durationModalUp);

    &--open {
        z-index: 500;
        opacity: 1;
        @include transition(opacity $durationModal);
        cursor: pointer; }

    &__whiteout {
        @include position(absolute, 0 null null 0);
        @include size(100%);
        background-color: $colorWhiteout; }

    &__inner {
        @include position(absolute, 50% null null 50%);
        @include transform(translate(-50%, -50%) scale(2, 2));
        width: 32em;
        max-width: 100%;
        box-shadow: $shadowDown;
        background-color: $colorDarkBG2;
        padding: 2em 2em 6em;
        color: $colorLightText;
        @include transition(all $durationModal);
        opacity: 0;
        cursor: default;

        &--open {
            opacity: 1;
            @include transform(translate(-50%, -50%) scale(1, 1)); } }

    &__heading {
        font-size: 2em;
        font-weight: $fontWeightLight;
        color: $colorEmphasis;
        margin-bottom: 1.5em; } }

.modal-buttons {
    text-align: right;
    padding: 0 2em;
    @include position(absolute, null null 0 0);
    height: 2.5em;
    width: 100%;

    &--separated {
        border-top: 1px solid $colorDarkBG3; } }
