$navigationWidth: 22em;
$navigationWidthNarrow: 17em;
$moduleNavigationWidth: 5em;
$moduleNavigationWidthNarrow: 4em;
$navigationOffset: 12em;

$navigationModulesTop: 3.75em;
$navigationModulesTopNarrow: 1em;
$navigationModulesBottom: 6.5em;

.navigation-column {
    position: absolute;
    height: 100%;
    top: 0;

    &--modules {
        width: $navigationWidth;
        left: 0;
        padding-top: $navigationModulesTop;
        padding-bottom: $navigationModulesBottom;

        @media screen and (max-width: $thresholdMicro) {
            width: $navigationWidthNarrow;
            padding-top: $navigationModulesTopNarrow; } }

    &--nodes {
        width: $navigationWidth - $moduleNavigationWidth;
        left: $moduleNavigationWidth;
        background-color: $colorDarkBG2;
        box-shadow: $shadowLeft;

        @media screen and (max-width: $thresholdMicro) {
            left: $moduleNavigationWidthNarrow;
            width: $navigationWidthNarrow - $moduleNavigationWidthNarrow; } } }

.scroller--navigation-modules > .ps-scrollbar-y-rail {
    left: 0 !important;
    right: auto !important; }

.navigation-modules {
    width: $moduleNavigationWidth;

    @media screen and (max-width: $thresholdMicro) {
        width: $moduleNavigationWidthNarrow; } }

.navigation-module {
    margin-bottom: 0.5em;

    &__icon {
        font-size: 1.25em;
        line-height: 2.4em;
        width: 2.4em; }

    &__dropdown {
        right: auto;
        top: 0.25em;
        left: 4em;
        background-color: $colorDarkBG4; }

    &__info {
        background-color: $colorDarkBG3;
        color: $colorLightText; } }

.navigation-module-sub {
    border-bottom: 0.25em solid $colorDarkBG3;

    &__splitter {
        border-top: 1px solid $colorDarkBG3; }

    &__item a {
        color: $colorLightText; } }

.navigation-user {
    margin-top: 0.5em;

    &__avatar {
        display: inline-block;
        @include size(2.28571429em);
        border-radius: 50%;
        background-color: $colorDarkBG2;
        color: $colorEmphasis;
        overflow: hidden;
        line-height: 2.28571429em;
        cursor: pointer;
        font-size: 0.875em;
        font-family: $fontHeading;
        text-align: center;
        position: relative;

        img {
            @include position(absolute, 0 null null 0);
            z-index: 2;
            width: 100%; } } }

.navigation-brand {
    @include position(absolute, null null 0 0);
    width: $moduleNavigationWidth;
    height: $navigationModulesBottom;
    text-align: center;
    padding-top: 0.25em;

    @media screen and (max-width: $thresholdMicro) {
        width: $moduleNavigationWidthNarrow; }

    &__nuclear {
        color: $colorDarkBG4;
        display: block;
        position: relative;
        margin-bottom: 1.5em;

        &:after {
            content: '';
            display: inline-block;
            width: 1em;
            height: 1px;
            @include position(absolute, null null -1em 50%);
            margin-left: -0.5em;
            background-color: $colorDarkBG2; }

        img {
            width: 1.5em; }
        span {
            display: block;
            font-size: 0.625em;
            line-height: 1em;
            font-family: $fontHeading;
            font-weight: $fontWeightBold; } }

    &__kk {
        display: block;
        color: $colorDarkBG4;
        font-size: 0.875em; } }
