input:not([type="checkbox"]), textarea {
    width: 100%;
    height: 2.5em;
    background-color: transparent;
    font-size: 1em;
    color: $colorText;
    outline: none;
    font-family: $fontText;
    -webkit-appearance: none;
    border-radius: 0;

    @include placeholder {
        color: $colorMuted; } }

label {
    user-select: none; }

.content-inner {
    box-shadow: $shadowDownLight;
    background-color: $colorLightBG1;
    padding: 3em 2em 3em;
    @include clearfix;

    &__heading {
        font-size: 1.5em;
        font-weight: $fontWeightLight;
        margin-bottom: 1em; }

    &__row {
        margin-top: 3em;
        padding-top: 1.5em;
        border-top: 1px solid $colorLightBG4; }

    &--compact {
        padding-top: 1.5em; }

    @media screen and (max-width: $thresholdMicro) {
        padding: 3em 1em 3em; } }

.form-column, .form-group-column {
    float: left;
    vertical-align: top;
    display: inline-block; }

.form-column {
    &--full {
        width: 100%; }
    &--primary {
        width: 65%;
        @media screen and (max-width: $thresholdCollapse) {
            width: 100%; } }
    &--secondary {
        width: 35%;
        border-left: 1px solid $colorLightBG4;
        background-color: $colorLightBG2;
        @media screen and (max-width: $thresholdCollapse) {
            width: 100%;
            border-left: 0;
            border-top: 1px solid $colorLightBG4; } } }

.form-group-column {
    &--field {
        width: 65%;
        @media screen and (max-width: $thresholdMicro) {
            width: 100%; } }

    &--help {
        width: 35%;
        font-size: 0.75em;
        color: $colorMutedLess;
        padding: 2.75em 0 0 1.33333333em;
        @media screen and (max-width: $thresholdMicro) {
            display: none; } } }

.form-buttons {
    @include position(fixed, null 2em 1em null);
    box-shadow: $shadowDownLight;
    background-color: $colorLightBG1;

    @media screen and (max-width: $thresholdMicro) {
        right: 1em; } }

.form-group {
    @include clearfix;
    margin: 0.5em 0 1.5em;

    &--inline {
        display: inline-block;
        min-width: 24%; }

    &__errors {
        text-align: right;
        font-size: 0.75em;
        color: $colorDanger;
        padding: 0.25em 0; }

    &__label {
        text-align: left;
        font-size: 0.875em;
        font-weight: 700;
        font-family: $fontHeading;
        color: $colorMuted;
        @include transition(color $durationShort);
        padding-left: 1em;
        position: relative;
        display: block;
        line-height: 1.25em;

        &:before {
            content: '';
            width: 0.75em;
            height: 1px;
            position: absolute;
            bottom: 0.25em;
            border-bottom: 1px solid $colorOutline;
            left: 0;
            @include transition(border-bottom-color $durationShort); }

        &--focus {
            color: $colorEmphasis;
            &:before {
                border-bottom-color: $colorEmphasis; } }

        &--error {
            color: $colorDanger;
            &:before {
                border-bottom-color: $colorDanger; } } }

    input, textarea {
        border-bottom: 1px solid $colorLightBG4;
        @include transition(border-bottom $durationShort);

        &:focus {
            border-bottom: 1px solid $colorEmphasis; } }

    textarea {
        max-width: 28em;
        min-height: 6em;
        resize: vertical; }

    &__select {
        position: relative;
        border-bottom: 1px solid $colorLightBG4;

        i {
            @include position(absolute, 0.625em 0.375em null null);
            color: $colorOutline;
            font-size: 0.875em;
            @include transition(color $durationShort); }

        select {
            height: 2em;
            position: relative;
            width: 100%;
            z-index: 1;
            cursor: pointer;
            background: transparent;
            -webkit-appearance: none;
            border: 0;
            box-shadow: none;
            outline: none;
            padding-right: 1.5em;
            font-size: 1em;
            color: $colorText; } }

    &__checkbox {
        span {
            color: $colorDanger;
            @include transition(color $durationShort);
            .icon-confirm {
                display: none; } }

        input {
            display: none;

            &:checked + span {
                color: $colorEmphasis;
                .icon-confirm {
                    display: inline-block; }
                .icon-cancel {
                    display: none; } } } }

    &__password {
        background-color: $colorLightBG3;
        height: 0.25em;
        opacity: 0.7;

        .password-strength {
            height: 0.25em;
            width: 0%;
            @include transition(all 150ms);

            &--veryweak {
                background-color: $colorDanger; }
            &--weak {
                background-color: #e67e22; }
            &--medium {
                background-color: $colorEmphasis; }
            &--strong {
                background-color: #2ecc71; } } }

    &--error {
        input, textarea {
            border-bottom-color: $colorDanger; } }

    &--inverted {
        input, textarea, .form-group__select {
            border-bottom-color: $colorOutline; }

        input:not([type="checkbox"]), textarea, select {
            color: $colorLightText; } } }
