.content-list-container {
    position: relative;
    background-color: $colorLightBG1;
    box-shadow: $shadowDownLight;
    width: calc(100% - 4em);
    margin: 4.25em auto 0;

    &--sub {
        margin-top: 0;
        box-shadow: none; }

    @media screen and (max-width: $thresholdCollapse) {
        width: 100%; } }

.content-list {
    width: 100%;
    position: relative;
    top: -2.75em;

    a {
        display: inline-block;
        color: $colorText;
        @include transition(color $durationShort); }

    &__thumbnail {
        @include position(absolute, 0.625em null null 1em);
        z-index: 49;
        @include size(2em);
        @include transition(opacity $durationShort); }

    &__checkbox {
        opacity: 0;
        @include transition(opacity $durationShort);
        cursor: pointer;
        position: relative;
        z-index: 50;

        &:checked {
            opacity: 1;

            & + .content-list__thumbnail {
                opacity: 0.25; } } }

    &__no-results {
        text-align: center;
        padding: 3em 1em; }

    &__row {
        line-height: 1.5em;
        &--body {
            border-bottom: 1px solid $colorLightBG4;
            @include transition(border-bottom-color $durationShort, background-color $durationShort); } }

    &__cell {
        font-size: 0.875em;
        padding: 0.75em 1em 0.75em 0;
        color: $colorMutedLess;
        vertical-align: top;
        @include clearfix;

        &--thumbnail {
            width: 4em;
            text-align: center;
            padding-left: 1em;
            position: relative; }

        &--options {
            width: 3em;
            padding: 0;

            .dropdown {
                font-size: 1.14285714em; }

            .dropdown-icon {
                line-height: 3.25em;
                width: 3.5em; } }

        &--head {
            padding: 1.2em 1.2em 0.5em 0;
            vertical-align: bottom;
            text-align: left;
            font-size: 0.75em; }

        @media screen and (max-width: $thresholdCollapse) {
            &--secondary {
                display: none; } } } }

.content-message {
    text-align: center;
    font-size: 1.25em;
    padding: 3em 0; }

.content-list-container--sub .content-list {
    top: 0; }

.sortable-links {
    font-size: 0.75em;

    .sortable-link {
        margin-right: 2em; } }

.sortable-link {
    color: $colorText;
    @include transition(color $durationShort);
    font-weight: $fontWeightBold;
    font-family: $fontHeading;

    &--active {
        color: $colorEmphasis !important;

        &:after {
            display: inline-block;
            content: '';
            margin-left: 0.5em;
            margin-bottom: 0.25em;
            height: 0.3em;
            width: 0.6em;
            border-left: 0.3em solid transparent;
            border-right: 0.3em solid transparent; } }

    &--asc:after {
        border-bottom: 0.3em solid $colorEmphasis; }

    &--desc:after {
        border-top: 0.3em solid $colorEmphasis; } }

.content-footer {
    width: 100%;
    text-align: center;
    padding: 1em;

    &--table {
        @include position(absolute, null 0 0 null);
        height: 2.75em;
        padding: 0.125em 1em;
        background-color: $colorEmphasis; } }

.pagination-container {
    padding: 0.5em 0; }

.pagination {
    &__sibling, &__current-container {
        display: inline-block;
        vertical-align: top;
        @include transition(opacity $durationShort); }

    &__sibling {
        padding: 0.0625em 0.5em 0;
        color: $colorEmphasisDarker;
        &--disabled {
            color: transparent !important; } }

    &__current-container {
        border-radius: 0.5em;
        height: 1.5em;
        line-height: 1.5em;
        color: $colorLightText;
        background-color: $colorEmphasisDark;
        padding: 0 2em;
        position: relative; }

    &__current {
        display: inline-block;
        font-size: 0.625em;
        font-family: $fontHeading;
        font-weight: $fontWeightBold;
        line-height: 2em;
        vertical-align: text-top; }

    &__selector {
        @include position(absolute, 0 null null 0);
        @include size(100%);
        opacity: 0;
        cursor: pointer;
        z-index: 10; }

    &--inpage {
        .pagination {
            &__sibling {
                color: $colorMutedLess; }
            &__current-container {
                background-color: $colorMuted; } } } }

.no-touch {
    .content-list__row--body:hover {
        border-bottom-color: $colorEmphasis;
        background-color: $colorEmphasisMuted;

        .content-list__checkbox {
            opacity: 1; }

        .content-list__thumbnail {
            opacity: 0.25; }

        .dropdown-icon {
            color: $colorEmphasis; } }

    .content-list a:hover, .sortable-link:hover {
        color: $colorEmphasis; }

    .pagination {
        &__sibling:hover, &__current-container:hover {
            opacity: 0.7; } } }
